import React from "react";
import { screen, render, fireEvent } from "@testing-library/react";
import PaymentScreen from "./../../PaymentScreen";
import "@testing-library/jest-dom/extend-expect";
import { Provider } from "react-redux";
import { configureStore } from "@reduxjs/toolkit";
import { defineFeature, loadFeature } from "jest-cucumber";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  MemoryRouter,
  useLocation,
} from "react-router-dom";
let store: any;
const feature = loadFeature("src/components/features/paymentscreen.feature");
defineFeature(feature, (test) => {
  test("User pays for occupied parking spot", ({ given, then, when, and }) => {
    given("I am on the Payment Screen", () => {
      const state = {
        spaceDetails: {
          registration: "ABC123",
          parkingTime: "08:30",
        },
        space: 1,
      };
      render(
        <MemoryRouter initialEntries={["/payment"]}>
          <Wrapper>
            <Routes>
              <Route path="/payment" element={<PaymentScreen />} />
            </Routes>
          </Wrapper>
        </MemoryRouter>
      );
    });

    when("I click on the Make Payment button", async () => {
      const paymentButton = screen.getByTestId("paymentButton");
      fireEvent.click(paymentButton);
      await screen.findByText("Payment Successful!");
    });

    function Wrapper({ children }: { children: React.ReactNode }) {
      const location = useLocation();
      return <div>{children}</div>;
    }
  });
});
